import React, { Component } from "react";

import geah_slideshow_combo_1 from "../images/slideshow/geah_slideshow_combo_1.jpg"
import geah_slideshow_combo_2 from "../images/slideshow/geah_slideshow_combo_2.jpg"
import geah_slideshow_combo_3 from "../images/slideshow/geah_slideshow_combo_3.jpg"

import geah_logo_pharm_4 from "../images/geah_logo_pharm_4.png"


import "../style.css"

import geah_logo_pharm_3 from "../images/geah_logo_pharm_3.png"
import geah_logo_sb from "../images/geah_logo_sb.png"
import geah_logo_estetica_2 from "../images/geah_logo_estetica_2.jpeg"


import {
  NavLink,
} from "react-router-dom"

class Home extends Component {
  render() {
    return (
<div className="fade-in" >
<div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
  <ol className="carousel-indicators">
    <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
    <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
    <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
  </ol>
  <div className="carousel-inner">

    <div className="carousel-item active">
      <img src={geah_logo_pharm_4}  className="d-block w-100" alt="First slide" />
    </div>

    <div className="carousel-item ">
      <img src={geah_slideshow_combo_1}  className="d-block w-100" alt="Second slide" />
    </div>
    <div className="carousel-item">
      <img src={geah_slideshow_combo_2}  className="d-block w-100" alt="Third slide" />
    </div>
    <div className="carousel-item">
      <img src={geah_slideshow_combo_3}  className="d-block w-100" alt="Fourth slide" />
    </div>
  </div>
  <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
    <span className="sr-only">Previous</span>
  </a>
  <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
    <span className="carousel-control-next-icon" aria-hidden="true"></span>
    <span className="sr-only">Next</span>
  </a>
</div>
<div className="container " >


  <div className="row text-center fade-in-3" >
    <div className="col-sm-4 p-3">
    <NavLink style={{color:"white"}} to="/pharm">
    <img src={geah_logo_pharm_3} height={200}className="p-1 rounded-circle shadow-lg rot1 " alt="GEAH Pharm logo" />
    </NavLink>


    </div>
    <div className="col-sm-4 p-3">
      <NavLink style={{color:"white"}} to="/salon">

    <img src={geah_logo_sb} height={200} className="p-1 rounded-circle shadow-lg  rot1" alt="GEAH Salon y Belleza logo" />
    </NavLink>
    </div>


    <div className="col-sm-4 p-3">
    <NavLink style={{color:"white"}} to="/estetica">

    <img src={geah_logo_estetica_2} height={200} className="p-1 shadow-lg rounded-circle  rot1  " alt="GEAH Medicina Esteticalogo" />
    </NavLink>

    </div>
  </div>
</div>
</div>





    );
  }
}

export default Home;